.empty-place {
  position: absolute;
  background-color: #fff5;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: var(--player-width);
  padding: 5px;
  border-radius: 10px;
  z-index: 1000;
}

.empty-place__button {
  position: relative;
  width: 150px;
  height: 45px;
  margin: 15px;
  padding: 10px 5px;
  font-size: 18px;
  color: #fff;
  transition: all 0.3s ease;
  border-radius: 7px;
  overflow: hidden;
 
  background-color: #f00c0c;
  background: linear-gradient(to bottom, #f00c0c 0%, #680202 100%);
  border-top: 1px solid #f00c0c;
  border-bottom: 1px solid #680202;
}

.empty-place__button::before {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 200%;
  filter: blur(8px);
  background: linear-gradient(to left, transparent 0%, #fff 40%, #fff 60%, transparent 100%);
  transform: translate(-100%, -25%) rotate(10deg);
}

.empty-place__button:hover::before {
  opacity: 0.7;
  animation: light 3s;
}

.empty-place__button:focus,
.empty-place__button:hover {
  transform: scale(1.1);
}
